import React from 'react';
import DrumPad from './components/DrumPad';
import styled from 'styled-components';

const AppWrapper = styled.div `

    background-color: lightgray;
    display: grid;
    grid-template-rows: 180px;
    justify-content: center;
    min-height: 100vh;
    width: 100vw;
    
    h1.heading{
        align-items: center;
        background-color: green;
        display: grid;
        height: 150px;
        justify-items: center;

        @media(max-width: 768px){
            font-size: 1.7rem;
        }

        @media(max-width: 415px){
            font-size: 1.3rem;          
        }
    }

    .drum-pad{
        align-content: space-around;
        background-color: lightgreen;
        border: 2px solid black;
        border-radius: 8px;
        display: grid;
        grid-gap: 10px;
        grid-template-columns: repeat(3, 120px);
        grid-template-rows: repeat(3, 120px);
        height: 400px;
        justify-items: center;
        padding: 10px;
        

        @media(max-width: 768px){
            grid-template-columns: repeat(3, 100px);
            grid-template-rows: repeat(3, 100px);
            height: 340px;
        }

        @media(max-width: 415px){
            grid-template-columns: repeat(3, 80px);
            grid-template-rows: repeat(3, 80px);
            height: 270px;         
        }
    }

`

const data = [
    {id: 'Bass', letter: 'Q', src: 'http://dight310.byu.edu/media/audio/FreeLoops.com/5/5/Moog%20Bass%20Hit%2010-2790-Free-Loops.com.mp3'},
    {id: 'Drum Bass', letter: 'W', src: 'http://dight310.byu.edu/media/audio/FreeLoops.com/1/1/Bass%20Drum%20Sound-21786-Free-Loops.com.mp3'},
    {id: 'Hi Hat', letter: 'E', src: 'http://dight310.byu.edu/media/audio/FreeLoops.com/1/1/909%20Hi%20Hat%2005-5835-Free-Loops.com.mp3'},
    {id: 'Snare Drum 1', letter: 'A', src: 'http://bigsamples.free.fr/d_kit/snare/606snare.wav'},
    {id: 'Snare Drum 2', letter: 'S', src: 'http://dight310.byu.edu/media/audio/FreeLoops.com/1/1/Alchemist%20Snare%201-1830-Free-Loops.com.mp3'},
    {id: 'Gun Shot', letter: 'D', src: 'http://www.chiptape.com/chiptape/sounds/medium/Track%2025.mp3'},
    {id: 'Guitar Strum', letter: 'Z', src: 'http://lapoc.de/samples/telshow-tele/cleanriff-E-01-12.wav'},
    {id: 'Drum Tom-tom', letter: 'X', src: 'http://www.burnkit2600.com/temp/HR-16/HR-16-WAVs/23-electronic%20tom.wav'},
    {id: 'Clap', letter: 'C', src: 'http://bigsamples.free.fr/d_kit/clap/cr8kclap.wav'}
]

class App extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            display: 'Click a button or press a key'
        }
    }

    handleDisplay = display => this.setState({ display })

    render() {
        return(
            <AppWrapper>
                <h1 className='heading'>{this.state.display}</h1>
                <div className='drum-pad'>{data.map(({ id, letter, src }) => (
                    <DrumPad 
                        handleDisplay={this.handleDisplay}
                        id={id}
                        key={id}
                        letter={letter}
                        src={src}
                    />
                ))}</div>
            </AppWrapper>
            
        )
    }
}

export default App;